import { calculateNiceNum } from './nicenum';
import { selectionToGroup } from './selectionToGroup';
import { canvasCreateMulti, processData, fetchData } from './common';

export default function (context) {
	const fetch = require('sketch-polyfill-fetch');
	const url = context.plugin.urlForResourceNamed("params.json");
	let result = NSString.stringWithContentsOfFile_encoding_error(url, NSUTF8StringEncoding, null);
	let params = JSON.parse(result);
	let canvas = canvasCreateMulti(context);
	canvas.doc.currentPage().changeSelectionBySelectingLayers_([]);

	function createProgressChart(Num) {

		if (canvas.height[Num] === canvas.width[Num]){

	    	let radius = canvas.width[Num] / 2 - params.progressParams.progressBarBorder / 2,
				x0 = canvas.x[Num] + canvas.width[Num] / 2,
				y0 = canvas.y[Num],
				yCenter = y0 + canvas.width[Num] / 2,
				startAngle = -90,
				endAngle = dataObj.table[Num] * 3.6 - 90;

			let arc = NSBezierPath.bezierPath();
			arc.appendBezierPathWithArcWithCenter_radius_startAngle_endAngle(NSMakePoint(x0,yCenter), radius, startAngle, endAngle);

			// Create shape from path
			let newBezier = MSPath.pathWithBezierPath(arc);
			var arcShape = MSShapeGroup.shapeWithBezierPath(newBezier);
			var border = arcShape.style().addStylePartOfType(1);
			border.color = MSColor.colorWithRed_green_blue_alpha(params.progressParams.progressBarColor[0]/255,params.progressParams.progressBarColor[1]/255,params.progressParams.progressBarColor[2]/255,1);
			border.thickness = params.progressParams.progressBarBorder;
			arcShape.setName("c_progress_" + ( 1 ));
			let arcArray = NSArray.arrayWithArray([arcShape]);


			// Add arc on artboard
			if (canvas.doc.currentPage().currentArtboard() === null){
				canvas.doc.currentPage().addLayers(arcArray);
			} else{
				canvas.doc.currentPage().currentArtboard().addLayers(arcArray);
			}

			arcShape.select_byExpandingSelection(true, true);


		} else{

			let progressHeight = canvas.height[Num],
				progressWidth = canvas.width[Num],
				x0 = canvas.x[Num] + progressHeight / 2,
				y0 = canvas.y[Num] + progressHeight / 2,
				progressBar = dataObj.table[Num] * (progressWidth / 100);

			let bar = NSBezierPath.bezierPath();
			bar.moveToPoint(NSMakePoint(x0,y0));
			bar.lineToPoint(NSMakePoint(x0 + progressBar - progressHeight,y0));

			// Create shape from path
			let newBezier = MSPath.pathWithBezierPath(bar);
			let barShape = MSShapeGroup.shapeWithBezierPath(newBezier);
			let border = barShape.style().addStylePartOfType(1);
			barShape.style().borderOptions().lineCapStyle = 2;
			border.color = MSColor.colorWithRed_green_blue_alpha(params.progressParams.progressBarColor[0]/255,params.progressParams.progressBarColor[1]/255,params.progressParams.progressBarColor[2]/255,1);
			border.thickness = progressHeight;
			barShape.setName("c_progress_" + ( 1 ));
			let barArray = NSArray.arrayWithArray([barShape]);

			// Add arc on artboard
			if (canvas.doc.currentPage().currentArtboard() === null){
				canvas.doc.currentPage().addLayers(barArray);
			} else{
				canvas.doc.currentPage().currentArtboard().addLayers(barArray);
			}

			barShape.select_byExpandingSelection(true, true);


		}

		canvas.layer[Num].select_byExpandingSelection(true, true);
		selectionToGroup(canvas, "Progress");

	}

	function drawChart(){
		for (let i = 0; i < canvas.layer.length; i++) {
			let maxSpark = Math.max.apply(null, dataObj.table[i]);
			let minSpark = Math.min.apply(null, dataObj.table[i]);
			let niceScales = calculateNiceNum( minSpark, maxSpark );
			dataObj.niceMax = niceScales.niceMaximum;
			dataObj.niceMin = niceScales.niceMinimum;

			if (canvas.layer[0].isKindOfClass(MSShapeGroup)) {
				createProgressChart(i);
			} else {
				let plot = canvas.layer[i].firstLayer();

				canvas.height[i] = plot.frame().height();
				canvas.width[i] = plot.frame().width();
				canvas.x[i] = plot.frame().x() + canvas.layer[i].frame().x();
				canvas.y[i] = plot.frame().y() + canvas.layer[i].frame().y();

	            plot.duplicate();
	            let layersInGroup = canvas.layer[i].containedLayersCount();
	            canvas.layer[i].layerAtIndex(0).select_byExpandingSelection(true, true);

	            for (let z = 0; z < layersInGroup - 1; z++) {
					let layer = canvas.layer[i].layerAtIndex(1);
					layer.removeFromParent();
				}

				canvas.layer[i].ungroup();

				createProgressChart(i);
			}
		}
	}

	let dataObjArray = processData();
	let dataObj = 0;
	if (dataObjArray[1] === false) {
		dataObj = dataObjArray[0];
		drawChart();
	} else {
		fetchData(dataObjArray[0]).then(response => {
			dataObj = response;
			drawChart();
		})
	}

}